.r1v67ork {
  outline: none;

  &::after {
    content: '';
    position: absolute;
    inset: 0;
    box-shadow: inset 0 0 0 2px var(--selection-color);
    pointer-events: none;
    z-index: 2;
  }

  > .cj343x0:first-child {
    /* preserve left border on the first frozen cell after scrolling to the right */
    box-shadow: inset 2px 0 0 0 var(--selection-color);
  }
}

