.cwshxdl {
  &::before,
  &::after {
    content: '';
    position: absolute;
    background: grey;
  }

  &::before {
    left: 21px;
    width: 1px;
    height: 100%;
  }

  &::after {
    top: 50%;
    left: 20px;
    height: 1px;
    width: 15px;
  }

  &:hover {
    background: red;
  }
}

.cz4rbis {
  cursor: pointer;
  position: absolute;
  left: 21px;
  transform: translateX(-50%);
  filter: grayscale(1);
}

